@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	background-color: #24252A;
	transition: all 0.3s ease;
	font-family: "Montserrat", sans-serif;
	font-weight: 500;
	font-size: 14px;
}

:root {
	--background-color: #24252A;
	--main-color: #0088a9;
	--white-color: #edf0f1;
}

.whole-page {
	display: flex;
	height: 100vh;
	justify-content: flex-end;
	align-items: center;
	flex-direction: column;
}

.array-container {
	display: flex;
	align-items: flex-end;
	flex: 0.8 1;
	width: 85%;
}

.array-bar {
	flex: 1 1;
	transition-duration: 0s;
	width: 1px;
	background-color: #24252A;
	background-color: var(--background-color);
	display: inline-block;
	margin: 1px;
}

.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	padding: 30px 5%;
}

.button {
	background-color: transparent;
	border: 0;
	color: #edf0f1;
	color: var(--white-color);
	text-decoration: none;
	cursor: pointer;
}

.button:focus {
	outline: none;
	box-shadow: none;
}

.text-button:hover {
	color: #0088a9;
	color: var(--main-color);
}

.round-button {
	padding: 9px 25px;
	background-color: #0088a9;
	background-color: var(--main-color);
	border: none;
	border-radius: 50px;
}

.round-button:hover {
	background-color: rgba(0, 136, 169, 0.8);
}

.option-container{
	display: flex;
	color: #edf0f1;
	color: var(--white-color);
}

.slider-container label {
	margin: 5px 5px;
}

.slider-container .test {
	width: 15px;
	min-height: 10px;
}

.slider-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0 30px;
}

.slider-container .slider {
	position: flex;
	margin: 10px 50px;
	width: 100%;
	height: 10px;
	border-radius: 50px;
	-webkit-appearance: none;
	outline: none;
	background:rgb(49, 56, 63);
}

.slider-container .slider::-webkit-slider-thumb {
	-webkit-appearance: none;
	width: 15px;
	height: 15px;
	background-color: #0088a9;
	background-color: var(--main-color);
	border-radius: 50px;
}

.slider-container .slider::-webkit-slider-thumb:hover {
	box-shadow: 0 0 0 2px #edf0f1;
	box-shadow: 0 0 0 2px var(--white-color);
	color: transparent;
}

input[type="radio"] {
	display: none;
}

span{
	background-color: transparent;
}

.algorithm-container label{
	display: inline-block;
	padding: 10px 20px;
	background-color: #edf0f1;
	background-color: var(--white-color);
	color: #24252A;
	color: var(--background-color);
}

.algorithm-container label:hover{
	background-color: #0088a9;
	background-color: var(--main-color);
	transition: color 0s ease 0s;
	color: #edf0f1;
	color: var(--white-color);
	cursor: pointer;
}

.algorithm-container input:checked + label{
	background-color: #0088a9;
	background-color: var(--main-color);
	transition: color 0s ease 0s;
	color: #edf0f1;
	color: var(--white-color);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

